import { Component } from "react";
import lang from "../language/lang";
class Privacy extends Component{
    constructor(props){
        super(props);
        this.state={
            language:JSON.parse(localStorage.language).language
        }
    }
    render()
    {
        const{language}=this.state
        return(      
            <main id="privacy">
            <header>
                <h2>{lang[language].terms}</h2>
            </header>
            <section className="container">
                <div className="privacy-container">
                    <div className="privacy-title"><h4>{lang[language].terms}</h4></div>
                    <div className="privacy-content">
                        <p>XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX</p>
                        <h5>第1条</h5>
                        <p></p>
                    </div>
                    <a href="/register" className="yellow-btn">{lang[language].close}</a>
                </div>
            </section>
            </main>
        )
    }
}

export default Privacy;