import { Component } from "react";
import Header from './Header';
import lang from "../language/lang";
class Guide extends Component{
    constructor(props){
        super(props);
        this.state={
            language:JSON.parse(localStorage.language).language
        }
    }

    render()
    {
        const{language}=this.state
        return(
            <>
              <main id="guide">
                    <header>
                        <h2>{lang[language].guide}</h2>
                    </header>
                    <section className="container-main">
                        <Header/>
                        <div className="container1">
                            <div className="crumb">
                                <p><a href="">{lang[language].top}</a>{lang[language].guide}</p>
                            </div>
                            <div className="guide-container">
                                <div className="guide-card">
                                    <div>
                                        <h3>STEP1</h3>
                                        {/* <h4>見出し見出し見出し見出し見出し</h4>
                                        <img src="/assets/img/guide-img.png" alt="" className="guide-sp" />
                                        <p>本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文</p> */}
                                    </div>
                                    <img src="/assets/img/guide-img.png" alt="" className="guide-pc" />
                                </div>
                                <div className="guide-card">
                                    <div>
                                        <h3>STEP2</h3>
                                        {/* <h4>見出し見出し見出し見出し見出し</h4>
                                        <img src="/assets/img/guide-img.png" alt="" className="guide-sp" />
                                        <p>本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文本文</p> */}
                                    </div>
                                    <img src="/assets/img/guide-img.png" alt="" className="guide-pc" />
                                </div>
                            </div>
                        </div>
                    </section>
                </main>
            </>
        )
    }
}

export default Guide;